/**
 * WordPress dependencies
 */
import { registerStore } from '@safe-wordpress/data';

/**
 * External dependencies
 */
import type {
	ConversionActionType,
	ConversionActionTypeName,
	Dict,
	Maybe,
	RemoveReturnTypes,
	OmitFirstArgs,
} from '@nab/types';

/**
 * Internal dependencies
 */
import * as selectors from './selectors';
import * as actions from './actions';
import { reducer } from './reducer';
import { STORE_NAME } from './config';

export { STORE_NAME } from './config';

registerStore( STORE_NAME, { reducer, selectors, actions } );

// ==========
// TYPESCRIPT
// ==========

type Actions = RemoveReturnTypes< typeof actions >;

type Selectors = OmitFirstArgs<
	Omit< typeof selectors, 'getConversionActionType' >
> & {
	readonly getConversionActionType: < T extends Dict = Dict >(
		name: ConversionActionTypeName
	) => Maybe< ConversionActionType< T > >;
};

declare module '@wordpress/data' {
	function dispatch( key: typeof STORE_NAME ): Actions;
	function select( key: typeof STORE_NAME ): Selectors;
}
